@font-face {
  font-family: "BunchBlossomsPersonalUse";
  src: url("BunchBlossomsPersonalUse-0nA4.ttf") format("truetype");
}

.links {
  /*text-align: center;*/
  display: flex;
  justify-content: space-between;
}

.logoWrapper {
  padding-left: 60px;
}

.authorText {
  font-family: BunchBlossomsPersonalUse;
}

.navDiv {
  display: flex !important;
  justify-content: space-between !important;
}

nav {
  background-image: url("background.jpg");
}

.nav a {
  color: #1e2125;
  font-weight: bold;
  text-shadow: 1px 1px 2px grey;
  transition: text-shadow 0.2s ease-in-out, color 0.5s;
}

.nav a:hover {
  color: #dde0e3;
  text-shadow: 0px 0px 4px black;
}

